.App {
  height: 100vh;
  min-width: 1270px;
  min-height: 780px;
  overflow: hidden;
  background-color: #f5f5f5;
  background: linear-gradient(109.8deg, #f5f5f5 0%, #e0e0e0 100%);
  position: relative;
  z-index: 100;
}

.instagram-handle-container {
  position: absolute;
  top: 50px;
  right: 50px;
  text-decoration: none;
}

.instagram-handle {
  width: 385px;
  height: 84px;
  background-color: #3c91df;
  border-radius: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
  box-shadow: 5px 20px 40px rgba(60, 145, 223, 0.24);
  border-radius: 28px;
}

.instagram-handle__text {
  color: white;
  font-family: "Inter", sans-serif;
  font-style: normal;
  font-size: 30px;
  font-weight: 400;
  letter-spacing: 0.09rem;
}

.blurb-container {
  position: absolute;
  left: 60px;
  bottom: 60px;
}

.blurb {
  position: relative;
  width: 656px;
  height: 638px;

  padding: 80px 64px;
  box-sizing: border-box;

  display: flex;
  flex-direction: column;
  gap: 40px;

  position: relative;

  background-color: white;
  background: rgba(255, 255, 255, 0.85);
  box-shadow: 0px 4px 168px rgba(0, 0, 0, 0.15);
  backdrop-filter: blur(25px);
  /* Note: backdrop-filter has minimal browser support */

  border-radius: 50px;
}

.free-tooltip-container {
  position: absolute;
  right: -63px;
  top: -32px;
}

.free-tooltip {
  position: relative;
  width: 174px;
  height: 83.67px;
  padding-left: 3px;
  box-sizing: border-box;
  background: #e58411;

  font-family: DM Sans, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 47.6135px;
  color: white;
  letter-spacing: 0.04rem;

  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 37.9718px;
}

.free-tooltip__image {
  position: absolute;
  top: -11px;
  left: -20px;
  z-index: -1;
}

.blurb__header {
  margin: 0;
  width: 528px;
  height: 176px;
  left: 64px;
  top: 80px;

  font-family: Inter, sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 80px;
  line-height: 110%;
  /* or 88px */

  color: #1e1e1e;
}

.blurb__subtitle-container {
  left: 64px;
  top: 296px;
}

.blurb__subtitle {
  position: static;
  width: 445px;
  height: 126px;
  margin: 0;

  font-family: Inter, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 35px;
  line-height: 180%;
  /* or 63px */

  color: #1e1e1e;

  opacity: 0.6;
}

.download-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 368px;
  height: 96px;

  background: #e58411;
  box-shadow: 5px 20px 40px rgba(229, 132, 17, 0.24);
  border-radius: 28px;

  text-decoration: none;
  color: white;

  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  font-size: 40px;
  letter-spacing: 0.015rem;
}
